.footer {
	padding-top: rem(72);

	&__top {
		padding-bottom: rem(75);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		>.flex {
			flex: 0 0 100%;
			justify-content: space-between;
		}

		&-nav {
			flex: 0 0 100%;
			max-width: 100%;
			justify-content: space-between;
			.items {
				width: auto;
				flex-direction: column-reverse;
				align-self: baseline;

				.item {
					flex: 0 0 auto;
					max-width: 100%;
					align-self: flex-start;
					&:first-child {
						margin-bottom: 0;
					}
					&:last-child {
						margin-bottom: rem(36);
					}	
				}
			}
			.item {
				margin-bottom: rem(36);
				flex: 0 0 calc((100% - 64px) / 3);
				max-width: calc((100% - 64px) / 3);

				align-self: flex-start;

				.title {
					font-size: rem(14);
					margin-bottom: rem(24);
				}

				ul {

					li {

						a {
							font-size: rem(15);
						}
					}
				}

			}
		}

		&-left {
			.logo {
				max-width: 100%;
			}

			.links {
				margin-bottom: rem(24);

				a {
					color: white;
					display: block;

					&:first-child {
						font-size: rem(24);
						margin-bottom: rem(4);
					}

					&:last-child {
						font-size: rem(20);
					}
				}
			}

			.copy {
				margin-bottom: rem(48);
				span {
					font-size: rem(22);
				}
			}
		}

		&-right {
			display: block;

			.form {
				position: relative;
				margin-top: rem(10);
				display: flex;
				flex-direction: column;

				.label {
					font-size: rem(15);
					line-height: 139%;
					color: white;
					margin-bottom: rem(22);
				}

				.field_set {

					.field_error {
						left: 0;
						bottom: rem(2);
						color: white;
					}

					input {
						width: 100%;
						color: white;
						font-size: rem(15);
						font-weight: 300;
						background-color: transparent;
						padding: rem(11) rem(20) rem(11) 0;

						border-bottom: rem(1) solid rgba(229,232,236,.3);

						@include placeholder {
							color: white;
						}
					}

					button {
						position: absolute;
						top: rem(5);
						right: 0;
						width: rem(32);
						height: rem(32);

						display: flex;
						align-items: center;
						justify-content: flex-end;

						i {
							color: white;
							font-size: rem(28);
						}
					}
				}

				.form_success {
					position: absolute;
					top: rem(46);
					left: 0;

					display: none;
					justify-content: space-between;
					align-items: center;
					width: 100%;

					p {
						font-size: rem(14);
						color: white;
						font-weight: 500;
						max-width: 80%;
					}

					button {
						width: rem(32);
						height: rem(32);

						display: flex;
						align-items: center;
						justify-content: center;


						i {
							color: white;
							font-size: rem(24);

							&::before {
								color: white;
							}
						}
					}
				}
			}

			.form.form_send {
				.field_set {
					opacity: 0;
				}
				.form_success {
					display: flex;
				}
			}
		}

	}

	&__bottom {
		.links {
			a {
				font-size: rem(15);

				&:first-child {
					margin-right: rem(50);
					&::after {
						top: rem(4);
						right: rem(-25);
						height: rem(15);
					}
				}
			}
		}
	}
}