.slider {
	padding: rem(72) 0;

	h2 {
		margin-bottom: rem(60);
	}

	>.container {
		max-width: rem(492);
	}

	.swiper-container {
		max-width: rem(420);
	}
}

.swiper-page {
	margin-top: rem(46);

	.swiper-pagination-bullet {
		width: rem(12) !important;
		height: rem(12) !important;
		margin-left: 0 !important;
		margin-right: rem(20) !important;

		&:last-child {
			margin-right: 0;
		}
	}
}