.benefits {
	padding-bottom: rem(72);

	&__top {
		margin-bottom: rem(56);
		max-width: rem(478);
		margin-left: auto;
		margin-right: auto;
	}

	&__bottom {
		display: flex;
		flex-direction: column;
		padding-left: rem(58);
		padding-right: rem(58);
		&-items {
			margin-bottom: rem(56);

			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			.item {
				flex: 0 0 46%;
				max-width: 46%;

				&__info {

					h3 {
						font-size: rem(18);
					}

					p {
						font-size: rem(15);
					}
				}
			}
		}

		.button_secondary {
			width: auto;
			display: inline-block;
			// justify-content: center;

			margin-left: auto;
			margin-right: auto;
		}
	}
}