.subscribe {
	padding: rem(72) 0;

	>.container {
		padding-left: 0;
		padding-right: 0;
	}

	h2 {
		margin-bottom: rem(56);
	}

	&__form {

		.field {
			width: 80%;
			margin-bottom: 0;
			height: rem(56);
		}

		.button_secondary {
			position: absolute;
			top: 0;
			right: 0;
			padding: rem(16) rem(40);
			height: rem(56);
		}

		.form_success {
			p {
				max-width: rem(300);
			}
		}
	}

	.form_send_success {
		.button_secondary {
			opacity: 0;
		}

		.form_success {
			display: flex;
		}
	}

	&__list {
		justify-content: flex-start;
		margin-left: rem(27);

		.item {
			flex: 0 0 auto;

			margin-right: rem(30);

			&:nth-child(2),
			&:last-child {
				margin-right: rem(30);
			}

			span {
				font-size: rem(15);
			}
		}
	}
}