.articles {
	padding: rem(84) 0;

	h2 {
		text-align: center;
		margin-bottom: rem(56);
	}

	&__items {
		margin-bottom: rem(56);

		.item {
			&__top {
				height: rem(324);
			}
			&__bottom {
				.inside_title {
					font-size: rem(24);
					letter-spacing: rem(2);
				}
			}
		}
	}

	.button_primary {
		padding: rem(16) rem(40);
	}
}