.partners {
	padding: rem(72) 0;

	h2 {
		margin-bottom: rem(64);
	}

	&__items {
		padding: 0;

		.item {
			flex: 0 0 calc((100% - 15rem) / 3);
			max-width: calc((100% - 15rem) / 3);

			margin-right: rem(63);

			align-items: center;
			height: rem(54);

			margin-bottom: rem(36);

			&:first-child {
				order: 1;
			}

			&:nth-child(2) {
				order: 2;
			}

			&:nth-child(3) {
				margin-right: 0;
				margin-bottom: 0;
				order: 4;
				// justify-content: flex-end;
				
			}
			&:nth-child(4) {
				order: 3;
				margin-right: 0;
			}

			&:last-child {
				order: 5;
				margin-right: 0;
				margin-left: rem(63);
			}

			a {
				width: 100%;

				img {
					// width: 100%;
					// height: 100%
					// object-fit: cover;
				}
			}
		}
	}
}