@media (min-width: 768px) {
  h1 {
    font-size: 3rem;
  }
  p {
    font-size: 1.25rem;
  }
  .container {
    width: calc(100% - 112px);
  }
  .button_primary {
    font-size: 1rem;
    padding: 1.125rem 2.5rem;
  }
  h1 {
    font-size: 1.5rem;
    line-height: 150%;
    font-family: "Merriweather";
    color: var(--c-dark);
  }
  p.text-merri {
    font-size: 0.9375rem;
    line-height: 1.75rem;
    font-family: "Merriweather";
    color: var(--c-dark);
    letter-spacing: -0.0125rem;
  }
  h2 {
    font-size: 2.125rem;
  }
  .header {
    height: auto;
  }
  .header .logo {
    width: 12.75rem;
  }
  .header .logo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .header .header_bay i {
    font-size: 2.5rem;
  }
  .header__top {
    position: relative;
    top: auto;
  }
  .header_burger {
    width: 4.625rem;
    height: 4.625rem;
  }
  .header_burger__wrap {
    width: 2.875rem;
    height: 1.75rem;
  }
  .header_burger__wrap .line, .header_burger__wrap::before, .header_burger__wrap::after {
    height: 0.25rem;
  }
  .header__picture {
    display: block;
    margin-top: 1.875rem;
    margin-bottom: 1.5rem;
  }
  .header_menu__list,
  .header_menu .header_menu__child {
    padding-top: 6.25rem;
  }
  .header_menu_active .header_burger__wrap::before, .header_menu_active .header_burger__wrap::after {
    position: absolute;
    top: 1.875rem;
    left: 0.875rem;
  }
  .header_menu_active .header_burger__wrap::before, .header_menu_active .header_burger__wrap::after,
  .header_menu_active .header_burger__wrap .line {
    border-radius: 0;
    width: 2.875rem;
  }
  .header_menu_active_child .header_burger__wrap::before, .header_menu_active_child .header_burger__wrap::after {
    position: absolute;
    left: 0;
  }
  .header_menu_active_child .header_burger__wrap::before, .header_menu_active_child .header_burger__wrap::after,
  .header_menu_active_child .header_burger__wrap .line {
    border-radius: 0.0625rem;
    width: 1.625rem;
  }
  .header_menu_active_child .header_burger__wrap::before {
    top: 1.25rem;
    left: 1rem;
    transform: rotate(-45deg);
  }
  .header_menu_active_child .header_burger__wrap::after {
    top: 2.375rem;
    left: 1rem;
    transform: rotate(45deg);
  }
