    width: 0.75rem !important;
    height: 0.75rem !important;
    margin-left: 0 !important;
    margin-right: 1.25rem !important;
  }
  .swiper-page .swiper-pagination-bullet:last-child {
    margin-right: 0;
  }
  .numbers__top {
    position: relative;
  }
  .numbers__bottom {
    top: -3.625rem;
    background: url(../img/section_numbers_bg_x2.jpg) no-repeat top center;
    background-size: cover;
  }
  .numbers__bottom h2 {
    max-width: 30.125rem;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1.25rem;
  }
  .numbers__bottom p.text {
    font-size: 1.125rem;
    max-width: 34rem;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3.5rem;
  }
  .numbers__bottom-items {
