    margin-left: auto;
    margin-right: auto;
  }
  .news__bottom .button_primary {
    padding: 1rem 1.625rem;
  }
  .news__bottom p {
    font-size: 1.125rem;
  }
  .products {
    padding: 4.5rem 0;
  }
  .products h2 {
    margin-bottom: 3.5rem !important;
  }
  .products__items {
    margin-bottom: 3.5rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .products__items .item {
    flex: 0 0 49%;
    max-width: 49%;
