  .products__items .item__info p {
    font-size: 0.9375rem;
  }
  .products__items .item__info h3 br {
    display: block;
  }
  .products .button_primary {
    padding: 1rem 1.625rem;
  }
  .review {
    padding: 4.625rem 0;
  }
  .review__top {
    margin-bottom: 3.5rem;
  }
  .review__top h2 {
    margin-bottom: 2.25rem;
  }
  .review__bottom {
    margin-left: 5.625rem;
    margin-right: 5.625rem;
  }
  .review__bottom .button_secondary {
    padding: 1rem 1.625rem;
  }
  .review__bottom .swiper-wrapper {
    margin-bottom: 2.5rem;
  }
