    margin-right: 1.875rem;
  }
  .subscribe__list .item span {
    font-size: 0.9375rem;
  }
  .partners {
    padding: 4.5rem 0;
  }
  .partners h2 {
    margin-bottom: 4rem;
  }
  .partners__items {
    padding: 0;
  }
  .partners__items .item {
    flex: 0 0 calc((100% - 15rem) / 3);
    max-width: calc((100% - 15rem) / 3);
    margin-right: 3.9375rem;
    align-items: center;
    height: 3.375rem;
    margin-bottom: 2.25rem;
  }
  .partners__items .item:first-child {
    order: 1;
  }
  .partners__items .item:nth-child(2) {
    order: 2;
  }
  .partners__items .item:nth-child(3) {
    margin-right: 0;
    margin-bottom: 0;
    order: 4;
  }
  .partners__items .item:nth-child(4) {
    order: 3;
    margin-right: 0;
  }
  .partners__items .item:last-child {
    order: 5;
    margin-right: 0;
    margin-left: 3.9375rem;
  }
  .partners__items .item a {
    width: 100%;
  }
  .feedback {
    padding: 4.5rem 0;
    background-color: transparent;
    background: url(../img/p_bg.jpg) no-repeat top center;
    background-size: cover;
  }
  .feedback::before, .feedback::after {
    display: none;
  }
  .feedback > .container {
    width: 23.25rem;
    margin-left: auto;
    margin-right: auto;
  }
  .feedback .block_icon_text {
    display: none;
  }
  .feedback__form-header {
    height: 4.125rem;
  }
  .feedback__form-header h2 {
    font-size: 1.375rem;
  }
  .feedback__form-wrap .button_secondary {
    padding: 1.0625rem 0;
  }
  .feedback__text {
    font-size: 0.875rem;
    max-width: 100%;
    margin-bottom: 0;
  }
  .field_set .field_error {
    position: absolute;
    bottom: 0.3125rem;
    left: 0.8125rem;
    color: #EA374C;
    font-size: 0.625rem;
  }
  .field_subscribe {
    padding: 1.0625rem 0.75rem;
    font-size: 1rem;
    height: 3.5rem;
  }
  .popup_overlay__wrap {
    width: 29rem;
    margin: 0 auto;
  }
  .popup_overlay__wrap h3 {
    font-size: 1.5rem;
  }
  .popup_overlay__wrap p {
    max-width: 19.8125rem;
  }
  .cookie_info__first p {
    max-width: 40.625rem;
  }
  .cookie_info__last p {
    max-width: 40.625rem;
  }
  .footer {
    padding-top: 4.5rem;
  }
  .footer__top {
    padding-bottom: 4.6875rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .footer__top > .flex {
    flex: 0 0 100%;
    justify-content: space-between;
  }
  .footer__top-nav {
