  }
  .footer__top-nav .items {
    width: auto;
    flex-direction: column-reverse;
    align-self: baseline;
  }
  .footer__top-nav .items .item {
    flex: 0 0 auto;
    max-width: 100%;
    align-self: flex-start;
  }
  .footer__top-nav .items .item:first-child {
    margin-bottom: 0;
  }
  .footer__top-nav .items .item:last-child {
    margin-bottom: 2.25rem;
