  .footer__top-left .links a {
    color: white;
    display: block;
  }
  .footer__top-left .links a:first-child {
    font-size: 1.5rem;
    margin-bottom: 0.25rem;
  }
  .footer__top-left .links a:last-child {
    font-size: 1.25rem;
  }
  .footer__top-left .copy {
    margin-bottom: 3rem;
  }
  .footer__top-left .copy span {
    font-size: 1.375rem;
