  }
  .footer__top-right .form .field_set input {
    width: 100%;
    color: white;
    font-size: 0.9375rem;
    font-weight: 300;
    background-color: transparent;
    padding: 0.6875rem 1.25rem 0.6875rem 0;
    border-bottom: 0.0625rem solid rgba(229, 232, 236, 0.3);
