  .footer__top-right .form .field_set input::-webkit-input-placeholder {
    color: white;
  }
  .footer__top-right .form .field_set input:-moz-placeholder {
    color: white;
  }
  .footer__top-right .form .field_set input::-moz-placeholder {
    color: white;
  }
  .footer__top-right .form .field_set input:-ms-input-placeholder {
    color: white;
  }
  .footer__top-right .form .field_set button {
    position: absolute;
    top: 0.3125rem;
    right: 0;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .footer__top-right .form .field_set button i {
