    max-width: 80%;
  }
  .footer__top-right .form .form_success button {
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .footer__top-right .form .form_success button i {
    color: white;
    font-size: 1.5rem;
  }
  .footer__top-right .form .form_success button i::before {
    color: white;
  }
  .footer__top-right .form.form_send .field_set {
