	.header {
		height: auto;

		.logo {
			width: rem(204);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.header_bay {

			i {
				font-size: rem(40);
			}
		}

		&__top {
			position: relative;
			top: auto;
		}

		

		&_burger {
			width: rem(74);
			height: rem(74);

			&__wrap {
				width: rem(46);
				height: rem(28);

				.line,
				&::before,
				&::after {
					height: rem(4);
				}
			}
		}

		&__picture {
			display: block;
			margin-top: rem(30);
			margin-bottom: rem(24);
		}

		&_menu {
			&__list,
			.header_menu__child {
				padding-top: rem(100);
			}
		}
	}

.header_menu_active {

	.header_burger {
		&__wrap {

			&::before,
			&::after {
				position: absolute;
				top: rem(30);
				left: rem(14);
			}

			&::before,
			&::after,
			.line {
				border-radius: 0;
				width: rem(46);
			}
		}
	}
}

.header_menu_active_child {
	.header_burger {
		&__wrap {

			&::before,
			&::after {
				position: absolute;
				left: 0;
			}

			&::before,
			&::after,
			.line {
				border-radius: rem(1);
				width: rem(26);
			}

			&::before {
				top: rem(20);
				left: rem(16);
				transform: rotate(-45deg);
			}

			&::after {
				top: rem(38);
				left: rem(16);
				transform: rotate(45deg);
			}
		}
	}
}