.feedback {
	padding: rem(72) 0;

	background-color: transparent;
	background: url(../img/p_bg.jpg) no-repeat top center;
	background-size: cover;

	&::before, &::after {
		display: none;
	}

	> .container {
		width: rem(372);
		margin-left: auto;
		margin-right: auto;
	}

	.block_icon_text {
		display: none;
	}

	&__form {
		// width: rem(372);
		

		&-header {
			height: rem(66);

			h2 {
				font-size: rem(22);
			}
		}

		&-wrap {
			.button_secondary {
				padding: rem(17) 0;
			}
		}
	}

	&__text {
		font-size: rem(14);
		max-width: 100%;
		margin-bottom: 0;
	}
}