.products {
	padding: rem(72) 0;

	h2 {
		margin-bottom: rem(56) !important;
	}

	&__items {
		margin-bottom: rem(56);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.item {
			flex: 0 0 49%;
			max-width: 49%;

			

			&__info {

				p {
					font-size: rem(15);
				}

				h3 {
					br {
						display: block;
					}
				}
			}
		}
	}

	.button_primary {
		padding: rem(16) rem(26);
	}
}